import { Component, h, Prop, Element } from '@stencil/core';
import { cart } from '../../../global/data/cart';
import priceFormat from '../../../global/functions/price';

@Component({
	tag: 'ks-cart-discount-container',
	styleUrl: 'cartDiscountContainer.css'
})
export class CartDiscountContainer {
	@Element() root: HTMLElement;

	@Prop() codeBanner: string = "";
	@Prop() codePlaceholder: string = "";
	@Prop() pointsPlaceholder: string = "";
	@Prop() pointsMessage: string = "";
	@Prop() loginMessage: string = "";
	@Prop() infoMessage: string = "";

	@Prop() noPointsHeading: string = "";
	@Prop() noPointsMessage: string = "";

	@Prop() thresholdHeading: string = "";
	@Prop() thresholdMessage: string = "";

	@Prop() loginUrl: string = "";

	@Prop() loggedIn: boolean = false;
	@Prop() disablePoints: boolean = false;

	render() {
		const discount = cart.get("discount");
		const points = cart.get("points");
		const pointsForOrder = cart.get("pointsForOrder");
		const productValue = cart.get("productValue");

		if(discount && Object.keys(discount)?.length !== 0) {
			return (
				<nav class="ticket">
					<h2>{discount.heading}</h2>
					<ks-cart-discount-ticket
						name={discount.name}
						value={discount.value}
					></ks-cart-discount-ticket>
					
					{this.infoMessage != "" ?
						<ks-cart-discount-message>
							<span>{this.infoMessage}</span>
						</ks-cart-discount-message>
					: null}
				</nav>
			);
		}

		const pointsMessage = this.pointsMessage
			.replace("{available}", points.available.toString())
			.replace("{value}", priceFormat(points.value))
			.replace("{points}", pointsForOrder.toFixed(0));

		const loginMessage = this.loginMessage
			.replace("{points}", pointsForOrder.toFixed(0));

		const noPointsMessage = this.noPointsMessage
			.replace("{points}", pointsForOrder.toFixed(0));

		const thresholdMessage = this.thresholdMessage
			.replace("{threshold}", priceFormat(points.threshold))
			.replace("{points}", pointsForOrder.toFixed(0));
		
		const strings = cart.get("strings");
		
		return[
			<div class="code">
				<h2>{strings.discountCodeHeading}</h2>
				<ks-cart-discount-code
					placeholder={this.codePlaceholder}
					image={this.codeBanner}
				></ks-cart-discount-code>
			</div>,

			!this.disablePoints ?
				<div class="points">
					<h2>{strings.discountPointsHeading}</h2>
					{this.loggedIn ? (
						points.available > 0 ?
							productValue >= points.threshold || points.threshold == 0 ? 
								<ks-cart-discount-points
									placeholder={this.pointsPlaceholder}
									message={pointsMessage}
									points={points.available}
									orderPoints={pointsForOrder}
								></ks-cart-discount-points>
							:
								<ks-cart-discount-points-message
									heading={this.thresholdHeading}
									message={thresholdMessage}
									points={pointsForOrder}
								></ks-cart-discount-points-message>
						:
							<ks-cart-discount-points-message
								heading={this.noPointsHeading}
								message={noPointsMessage}
								points={pointsForOrder}
							></ks-cart-discount-points-message>
					) :
						<ks-cart-discount-points-login
							message={loginMessage}
							points={pointsForOrder}
							login-url={this.loginUrl}
						></ks-cart-discount-points-login>
					}
				</div>
			: null
		];
	}
}

