.cartSelect {
    position: relative;

    margin-bottom: 6px;
}

.cartSelect summary {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
    border: 1px solid #e5e5e5;
    background-color: white;

    list-style: none;
    font-size: 14px;
    transition: var(--transition-border-color);
}

.cartSelect summary:hover {
    border-color: #bebebe;
}

.cartSelect summary:active {
    border-color: #8d8d8d;
}

.cartSelect summary ks-icon {
    margin-right: 8px;
}

.cartSelect .items {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 1020;

    box-sizing: border-box;
    border: 1px solid #dadada;

    background: #fff;
    color: #373737;
    box-shadow: 0 5px 20px rgb(0 0 0 / 20%);
}

.cartSelect .item {
    display: block;
    position: relative;

    color: var(--color-dark);
    text-decoration: none !important;

    font-size: 14px;
    line-height: 14px;
    padding: 13px 5px 13px 10px;

    border: none;
    width: 100%;
    background: transparent;
    text-align: left;
}

.cartSelect .items .item.active {
    background-color: #f2f2f2 !important;
}

.cartSelect .items .item:hover { background-color: #e4e4e4 !important; }
.cartSelect .items .item:active { background-color: #d8d8d8 !important; }