import { Component, h, Listen, Element, Prop, State, Method } from '@stencil/core';
import { cart } from '../../../global/data/cart';
import * as cartFunctions from "../../../global/functions/cart";
import priceFormat from '../../../global/functions/price';

@Component({
  tag: 'ks-cart-select-payment',
  styleUrl: 'cartSelect.css'
})
export class CartSelectPayment {
    @Element() root: HTMLElement;
    @Prop({reflect: true}) name = "";
    @Prop({reflect: true, mutable: true}) valid: boolean = false;
    @Prop({reflect: true, mutable: true}) error: boolean = false;

    @Prop() PaymentChange?: (id: number) => void;

    @State() active: number = -1;
    @State() toggled = false;
    @State() loading = false;


    @Method() async StartLoading() {
        this.loading = true;
    }

    @Method() async ResetLoading() {
        this.loading = false;
    }

    @Method() async Validate() {
        if(!this.valid) {
            this.error = true;
            return false;
        }
        
        this.error = false;
        return true;
    }

    @Listen('click', { target: 'document' })
    OutsideClickHandler(event) {
        const target = event.target as HTMLElement;

        if(this.toggled && !target.closest(`.ks-cart-select[name="${this.name}"]`))
            this.root.querySelector("details").removeAttribute("open");
    }

    componentDidLoad() {
        this.root.classList.add("ks-cart-select");
    }

    ActivateItem(id: number) {
        cartFunctions.paymentChange(id);

        this.root.querySelector("details").removeAttribute("open");
        this.active = id;
        this.valid = true;
        this.error = false;
    }

    render() {
        if(this.valid)
            this.active = cart.get("activePayment");

        if(this.loading) return (
            <div class="center"> 
                <ks-loader/>
            </div>
        );

        const activeItem = cart.get("payment").find((value)=>{
            return value.id == this.active;
        });

        const strings = cart.get("strings");

        const paymentSelect = cart.get("payment").length > 0 ?
            strings.paymentSelect : strings.paymentMethodMissing;

        return <details class="cartSelect">
            <summary>
                {activeItem ? 
                    <div class="item">{activeItem?.name} <strong>&nbsp;{priceFormat(activeItem?.price)}</strong></div> :
                    <div class="item">{paymentSelect}</div>
                }
                <ks-icon name="chevron-down" size={0.8} aria-hidden/>
            </summary>

            <div class="items">
                {cart.get("payment").map((item) => 
                    <button key={item.id} class={this.active == item.id ? "active item" : "item"}
                        aria-selected={this.active == item.id}
                        onClick={()=>this.ActivateItem(item.id)}
                    >
                        {item.name} {item.price > 0 && <strong>&nbsp;{priceFormat(item.price)}</strong>}
                    </button>
                )}
            </div>
        </details>
    }
}