import { Component, h, Prop, Element } from '@stencil/core';
import { cart } from '../../../global/data/cart'
import * as cartFunctions from "../../../global/functions/cart";

@Component({
  tag: 'ks-cart-country-select',
  styleUrl: 'cartCountrySelect.css'
})
export class CartCountrySelect {
    @Element() root: HTMLElement;
    @Prop() heading: string;

    ChangeHandler(event: Event) {
        const target = event.target as HTMLSelectElement;
        cartFunctions.countryChange(target.value);
    }

    render() {
        return[
            <select onChange={(ev)=>this.ChangeHandler(ev)} aria-label="kraj">
                {cart.get('countries').map( country => 
                    <option value={country.value} selected={country?.selected}>{country.name}</option> 
                )}
            </select>,
            <ks-icon name="chevron-down"/>
        ];
    }

}