ks-cart-country-select {
    display: block;
    position: relative;
    margin-bottom: 7px;
}

ks-cart-country-select span:last-of-type { opacity: 0.0; }

ks-cart-country-select select {
    background: transparent;
    border: none;
    padding: 6px;

    -moz-appearance: none;
	-webkit-appearance: none;
	appearance: none;

    font-size: 16px;
    font-family: 'Nunito', sans-serif;

    color: #151515;
    transition: color 0.3s ease;

    width: 100%;
}

ks-cart-country-select ks-icon {
    position: absolute;
    right: 10px;
    top: 5px;
    pointer-events: none;
}

ks-cart-country-select option { font-size: 16px; }
ks-cart-country-select:hover select { color: #606060; }

ks-cart-country-select span:first-of-type { opacity: 0.0 !important; }
ks-cart-country-select span:last-of-type { opacity: 1.0 !important; }

ks-cart-country-select:hover span:first-of-type { opacity: 0.0; }
ks-cart-country-select:hover span:last-of-type { opacity: 1.0; }