ks-cart-buttons {
    display: block;
    position: relative;
    margin-top: 20px;

    user-select: none;
}

ks-cart-buttons > .confirm {
    flex: 1;
    width: 100%;

    font-size: 28px;
    font-weight: 700;
    padding: 20px;
}

ks-cart-buttons ks-button {
    height: 50px;
}

ks-cart-buttons ks-button button {
    font-weight: 700;
    font-size: 17px;
    font-family: var(--font-emphasis);
}

ks-cart-buttons .loading {
    pointer-events: none;
    opacity: 0.5;
}

ks-cart-buttons ks-loader {
    top: 23px;
}

ks-cart-buttons > * {
    transition: opacity 0.3s ease;
}